/* 
Universal CSS file for the entire project.
*/

body {
    background: #3494E6;
    /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #EC6EAD, #3494E6);
    /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #EC6EAD, #3494E6);
    /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    font-family: 'Maven Pro', sans-serif;
}

ul {
    padding: 0;
    list-style: none;
}

ol {
    padding: 0;
    list-style: none;
}

#button {
    border-radius: 25px;
    width: 100%;
    height: 40px;
    font-size: 1.3rem;
    color: white;
    font-weight: 1700;
    background: #2980b9;
    cursor: pointer;
    transition: opacity 0.25s ease-out;
    margin-top: 40px;
    margin-bottom: -10px;
    
}

#button:hover {
    opacity: 0.5;
}

#button {
    margin-left: 50%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 116px;
    height: 36px;
    text-align: center;
    line-height: 30px;
    color: #fff;
    font-size: 18px;
    text-transform: uppercase;
    text-decoration: none;
    font-family: 'Maven Pro', sans-serif;
    box-sizing: border-box;
    background: linear-gradient(90deg, #03a9f4, #f441a5, #ffeb3b, #03a9f4);
    background-size: 400%;
    border-radius: 12px;
}

#button:hover {
    animation: animate 8s linear infinite;
}

@keyframes animate {
    0% {
        background-position: 0%;
    }
    100% {
        background-position: 400%;
    }
}

#button:before {
    content: '';
    top: -5px;
    left: -5px;
    right: -5px;
    bottom: -5px;
    z-index: -1;
    background: linear-gradient(90deg, #03a9f4, #f441a5, #ffeb3b, #03a9f4);
    background-size: 400%;
    border-radius: 12px;
    opacity: 0;
}

#button:hover::before {
    filter: blur(20px);
}

